@import url(https://fonts.googleapis.com/css2?family=Sora:wght@200;400;600;700&display=swap);
body{background:#F3B8C6;margin:2rem auto;max-width:90%}.App{text-align:center}#preview,.title{display:inline-block}.colorScheme,.editorWrap textarea,.previewWrap{background-color:#fff;border:1px solid #d5244e;box-shadow:1px 1px 10px 2px #e46180;border-top:none}.toolbar{position:relative;background-color:#E7859A;padding:4px 4px 3px 3px;border:1px solid #d5244e;box-shadow:1px 1px 10px 2px #e46180;font-family:'Sora', 'Helvetica', sans-serif;font-weight:600;font-size:1rem;color:white}.toolbar i{width:25px;color:black;margin-left:5px}.toolbar .fa-arrows-alt,.toolbar .fa-compress{position:absolute;right:-5px}.fa-retweet:hover,.fa-compress:hover,.fa-arrows-alt:hover{color:#fae4e9;cursor:pointer}.fa-free-code-camp{margin-right:3px}.editorWrap{width:1fr;margin:1rem auto}.editorWrap .toolbar{width:99%}.editorWrap textarea{width:99%;min-height:200px;margin-bottom:-5px;resize:vertical;outline:none;padding-left:5px;padding-top:5px;font-size:12px}.converter{width:100px;text-align:center;font-size:35px;margin:auto}.previewWrap{width:1fr;margin:20px auto;min-height:200px;overflow-wrap:break-word;padding-right:20px}.previewWrap .toolbar{left:-1px;width:100%;padding-right:17px}.previewWrap #preview{margin-left:5px;margin-top:-10px;width:100%}.maximized{width:96%;min-height:100vh;margin:auto}.maximized textarea{min-height:95vh;resize:none}.hide{display:none}#preview blockquote{border-left:3px solid #E7859A;color:#E7859A;padding-left:5px;margin-left:25px}#preview code{background:white;padding:1px 4px 2px 4px;font-size:12px;font-weight:bold}#preview pre{background:white;padding:5px 0 5px 5px}#preview h1{border-bottom:2px solid #E7859A}#preview h2{border-bottom:1px solid #E7859A}#preview table{border-collapse:collapse}#preview td,#preview th{border:2px solid #E7859A;padding-left:5px;padding-right:5px}.footer{color:white;text-align:center;font-family:'Sora', 'Helvetica', sans-serif;margin:2rem 0}.footer a{color:#E7859A}

